<template>
  <div class="container">
    <el-button type="primary" class="addItem" @click="print()">打印</el-button>
    <h1>商品库存流水</h1>
    <div class="tableContainer">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-table
              :data="props.row.order_info"
              style="width: 100%">
              <el-table-column
                prop="sku"
                label="SKU">
              </el-table-column>
              <el-table-column
                prop="number"
                label="数量">
              </el-table-column>
              <el-table-column
                prop="code"
                label="区位">
              </el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <el-table-column
          label="ID"
          prop="id">
        </el-table-column>
        <el-table-column
          label="订单ID"
          prop="so_bill_id">
        </el-table-column>
        <el-table-column
          label="订单单号"
          prop="order_no">
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>

  export default {
    name: "warehouse",
    data() {
      return {
        tableData: [/*{
          "id": 13,
          "pick_round_id": "10",
          "so_bill_id": "1",
          "order_no": "od201804021123",
          "order_info": [
            {
              "sku": "sku",
              "number": "2",
              "code": "A-01-23"
            },
            {
              "sku": "sku2",
              "number": "3",
              "code": "A-02-22"
            }
          ]
        },
          {
            "id": 13,
            "pick_round_id": "10",
            "so_bill_id": "1",
            "order_no": "od201804021123",
            "order_info": [
              {
                "sku": "sku",
                "number": "2",
                "code": "A-01-23"
              },
              {
                "sku": "sku2",
                "number": "3",
                "code": "A-02-22"
              }
            ]
          }*/
        ],
        totalSize: 0,
        currentPage: 1,
        pageSize: 20
      }
    },
    methods: {
      filterDegree() {

      },
      filterStatus() {

      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },

      initTable() {
        let initList = [];
        let url = "http://api.orkowms.me/pick-round-detail?page=" + this.currentPage + "&per-page=" + this.pageSize;
        let row = this.$route.query;   
        if(row.id){
           url = url + "&pick_round_id="+row.id;
        }
        let that = this;
        let config = {
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
        };
        this.$axios.get(url, config).then((res) => {
          if (res.data.data) {
            let tasksList = res.data.data;
            that.totalSize = parseInt(res.data._meta.totalCount);
            tasksList.forEach((task) => {
              let tempTask = {};
              tempTask.id = task.id;
              tempTask.pick_round_id = task.pick_round_id;
              tempTask.so_bill_id = task.so_bill_id;
              tempTask.order_no = task.order_no;
              tempTask.order_info = task.order_info;
            
              initList.push(tempTask);
            })
          }
          that.tableData = initList;  
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    created() {
      this.initTable();
    }
  }
</script>

<style lang="stylus">
  .container
    padding 30px
    position relative
    .addItem
      margin 20px 0
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 148px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .demo-table-expand
          font-size: 0
          label
            width: 90px
            color: #99a9bf
          .el-form-item
            margin-right: 0
            margin-bottom: 0
            width: 33.333%
        td.markColor
          color #00a0e9
        a
          color #03B8CC
      .el-pagination
        text-align right
        margin 8px 0
</style>



